<template>
  <div id="app">
    <a-table :dataSource="data" :columns="columns" class="table">
    <template slot="name" slot-scope="text, record">
      <a :href=record.url>{{ text }}</a>
    </template>
    </a-table>
  </div>
</template>

<script>
import { getAPKList } from "@/service/getAPKlist";
const columns = [
  {
    title: '楚淮软件科技公司周东林的学习笔记|工作学习中遇到的问题和解决思路',
    dataIndex: "name",
    scopedSlots: { customRender: "name" },
  },
  {
    dataIndex: "tag",
    key:'tag',
    width: "8%",
  },
  {
    dataIndex: "addtime",
    key: "addtime",
    width: "15%",
  },
];
const data = [];
getAPKList().then(
  (res) => {
    const clist = res.data;
    for (var i of clist) {
      data.push({
        name: i.name,
        tag: i.tags,
        url: 'https://zhoudl.jschrj.com'+i.link,
        addtime: i.addtime,
      });
    }
  },
  () => {
    this.$message.info("后台接口连接异常");
  }
);
const zhoudl="楚淮软件科技公司周东林的学习笔记|工作学习中遇到的问题和解决思路"
export default {
  name: "App",
  data() {
    return {
      data,
      columns,
    };
  },
  methods: {

  },
};
</script>
<style>
.table{
  background: #fafafa;
  margin-top: auto;
  margin-left: auto;
}
</style>

